.movie-posters .movie-poster-container {
  position: relative;
  overflow: hidden;
  display: inline-block;
}
.movie-posters .movie-poster-container .placeholder-image ~ .title {
  opacity: 1.0;
}
.movie-posters .movie-poster-container .title {
  position: absolute;
  top: 25px;
  color: #f5f5f5;
  width: 100%;
  font-size: 22px;
  line-height: 24px;
  opacity: 0.0;
  font-weight: 100;
}
.movie-posters .movie-poster-container .announced-banner {
  color: #eeeeee;
  background-color: #777;
  -webkit-box-shadow: 2px 2px 20px #888888;
  -moz-box-shadow: 2px 2px 20px #888888;
  box-shadow: 2px 2px 20px #888888;
  -moz-transform-origin: 50% 50%;
  -webkit-transform-origin: 50% 50%;
  position: absolute;
  width: 320px;
  top: 200px;
  left: -122px;
  text-align: center;
  opacity: 0.9;
  filter: alpha(opacity=90);
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}
.movie-posters .movie-poster-container .released-banner {
  color: #eeeeee;
  background-color: #5cb85c;
  -webkit-box-shadow: 2px 2px 20px #888888;
  -moz-box-shadow: 2px 2px 20px #888888;
  box-shadow: 2px 2px 20px #888888;
  -moz-transform-origin: 50% 50%;
  -webkit-transform-origin: 50% 50%;
  position: absolute;
  width: 320px;
  top: 200px;
  left: -122px;
  text-align: center;
  opacity: 0.9;
  filter: alpha(opacity=90);
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}
.movie-posters .movie-poster-container .cinemas-banner {
  color: #eeeeee;
  background-color: #b94a48;
  -webkit-box-shadow: 2px 2px 20px #888888;
  -moz-box-shadow: 2px 2px 20px #888888;
  box-shadow: 2px 2px 20px #888888;
  -moz-transform-origin: 50% 50%;
  -webkit-transform-origin: 50% 50%;
  position: absolute;
  width: 320px;
  top: 200px;
  left: -122px;
  text-align: center;
  opacity: 0.9;
  filter: alpha(opacity=90);
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}
